.IntroBlock {
    position: relative;
    display: flex;
    flex-flow: row nowrap;
    width: 100%;
    height: auto;
    align-items: center;
    justify-content: center;
    margin-top: 1rem;
    margin-bottom: 0;
    div {
        display: flex;
        flex-flow: row nowrap;
        align-items: center;
        justify-content: center;
        width: 80%;
        height: 85%;
        margin-bottom: 2rem;
        h1 {
            font-size: 4vmin;
            text-align: center;
        }
    }
    &::after {
        content: '';
        height: 1px;
        width: 50%;
        position: absolute;
        bottom: 0;
        background: linear-gradient(to right, rgba(255, 255, 255, 0), rgba(255, 255, 255, 1), rgba(255, 255, 255, 1), rgba(255, 255, 255, 1), rgba(255, 255, 255, 1), rgba(255, 255, 255, 1), rgba(255, 255, 255, 1), rgba(255, 255, 255, 0));
    }
}

.larger {
    font-size: 12vmin;
}

.smaller {
    font-size: 1.5vmin;
}