.ProjBlock {
    width: 100%;
    margin-top: 1rem;
    display: flex;
    flex-flow: column nowrap;
    align-items: center;
    h1 {
        font-size: 8vmin;
    }
    .proj {
        display: flex;
        flex-flow: row nowrap;
        justify-content: space-evenly;
        position: relative;
        width: 100%;
        height: 30rem;
        margin-bottom: 2rem;
        div {
            width: 40%;
            display: flex;
            flex-flow: column nowrap;
            text-align: center;
            justify-content: center;
            a {
                font-weight: bold;
                font-size: 6vmin;
                transition: color 200ms;
                &:hover {
                    color: red;
                }
            }
            &::after {
                content: '';
                width: 1px;
                top: 25%;
                height: 75%;
                position: absolute;
                right: 50%;
                background: linear-gradient(to bottom, rgba(255, 255, 255, 0), rgba(255, 255, 255, 1), rgba(255, 255, 255, 1), rgba(255, 255, 255, 0));
            }
        }
    }
}